// pages/exams/[id]/start.js
import { useState, useEffect } from "react";
import { useRouter } from "next/router";
import Head from "next/head";
import AntiCheating from "../../../components/AntiCheating";
import { useSession } from "next-auth/react";
import { toast } from "sonner";
import { getServerSession } from "next-auth/next";
import { authOptions } from "../../";
import connectToDatabase from "../../../lib/db";
import Exam from "../../../models/exam";

export default function ExamPage({ exam }) {
  const { data: session, status } = useSession();
  const router = useRouter();

  // Ensure user is signed in. Redirect to /sign-in if not.
  useEffect(() => {
    if (status !== "loading" && !session) {
      router.push("/sign-in");
    }
  }, [session, status, router]);

  //---------------------------
  // Exam state & timer logic
  //---------------------------
  const [userAnswers, setUserAnswers] = useState({});
  const [submitted, setSubmitted] = useState(false);
  const [cheatingCount, setCheatingCount] = useState(0);
  const [timeTaken, setTimeTaken] = useState(0);

  const QUESTION_TIME = 20; // seconds per question
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [questionTimeLeft, setQuestionTimeLeft] = useState(QUESTION_TIME);

  useEffect(() => {
    if (submitted) return;
    
    const timer = setInterval(() => {
      setQuestionTimeLeft((prevTime) => {
        if (prevTime <= 1) {
          if (currentQuestionIndex === exam.questions.length - 1) {
            handleSubmit(); // Auto-submit on last question
            return 0;
          } else {
            proceedToNextQuestion();
            return QUESTION_TIME;
          }
        }
        return prevTime - 1;
      });
    }, 1000);
  
    return () => clearInterval(timer);
  }, [submitted, currentQuestionIndex]);

  const proceedToNextQuestion = () => {
    setTimeTaken((prev) => prev + (QUESTION_TIME - questionTimeLeft));

    if (currentQuestionIndex < exam.questions.length - 1) {
      setCurrentQuestionIndex((prev) => prev + 1);
      setQuestionTimeLeft(QUESTION_TIME);
    }
  };

  const handleOptionChange = (selectedOption) => {
    setUserAnswers((prev) => ({
      ...prev,
      [currentQuestionIndex]: selectedOption,
    }));

    // Automatically advance for non-final questions.
    if (currentQuestionIndex < exam.questions.length - 1) {
      proceedToNextQuestion();
    }
  };

  const handleSubmitClick = () => {
    handleSubmit();
  };

  const handleSubmit = async () => {
    if (submitted) return;
    setSubmitted(true);

    const correctCount = exam.questions.reduce((count, question, idx) => {
      return userAnswers[idx] === question.correctAnswer ? count + 1 : count;
    }, 0);
    const total = exam.questions.length;
    const passingScore = 40;
    const passed = correctCount >= passingScore;

    const resultData = {
      userId: session.user.id,
      examId: exam._id,
      userName: session.user.name,
      examName: exam.title,
      score: correctCount,
      total,
      passed,
      timeTaken,
      cheatingCount,
      createdAt: new Date(),
    };

    await onExamSubmit(resultData);
  };

  const handleCheatingDetected = () => {
    setCheatingCount((prev) => prev + 1);
  };

  if (!exam.questions || exam.questions.length === 0) {
    return <p>No questions found.</p>;
  }

  const currentQuestion = exam.questions[currentQuestionIndex];

  return (
    <div className="relative">
      <AntiCheating onCheatingDetected={handleCheatingDetected} />
      <div className="p-6 max-w-3xl mx-auto">
        <Head>
          <title>{exam.title}</title>
        </Head>
        <h1 className="text-3xl font-bold mb-6">{exam.title}</h1>
        <h2 className="text-xl mb-4">
          Question {currentQuestionIndex + 1} of {exam.questions.length}
        </h2>
        <div className="bg-white shadow-md rounded-lg p-4 mb-6 border border-gray-200">
          <p className="text-lg font-medium mb-3">{currentQuestion.questionText}</p>
          <div>
            {currentQuestion.options.map((option, i) => (
              <label
                key={i}
                className="block p-2 mb-2 border rounded cursor-pointer hover:bg-gray-100"
              >
                <input
                  type="radio"
                  name={`question-${currentQuestionIndex}`}
                  value={option}
                  onChange={() => handleOptionChange(option)}
                  checked={userAnswers[currentQuestionIndex] === option}
                  className="mr-2"
                />
                {option}
              </label>
            ))}
          </div>
          <div className="mt-3">
            <p className="text-sm text-gray-500">
              Time left for this question: {questionTimeLeft} seconds
            </p>
          </div>
          {currentQuestionIndex === exam.questions.length - 1 && (
            <button
              onClick={handleSubmitClick}
              className="px-6 py-2 mt-4 text-black bg-blue-500 hover:bg-blue-600 rounded shadow"
            >
              Submit
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

export async function getServerSideProps(context) {
  // Ensure the user is signed in. If not, redirect them.
  const session = await getServerSession(context.req, context.res, authOptions);
  if (!session) {
    return {
      redirect: {
        destination: "/sign-in",
        permanent: false,
      },
    };
  }

  try {
    await connectToDatabase();
    const { id } = context.params;
    const exam = await Exam.findById(id).lean();

    if (!exam || !exam.questions) {
      return { notFound: true };
    }

    // Convert fields to strings if necessary
    exam._id = exam._id.toString();
    if (exam.createdAt) exam.createdAt = exam.createdAt.toISOString();
    if (exam.updatedAt) exam.updatedAt = exam.updatedAt.toISOString();

    return { props: { exam } };
  } catch (error) {
    console.error("Error fetching exam:", error);
    return { notFound: true };
  }
}

async function onExamSubmit(resultData) {
  try {
    const resultRes = await fetch("/api/exams/result", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(resultData),
      cache: "no-store",
    });
    const resultJson = await resultRes.json();
    if (!resultRes.ok) {
      toast.error("Error submitting your exam results. Please try again later.");
      console.error(resultJson.message);
      return;
    }

    if (resultData.passed) {
      toast.success("Congratulations! You passed the exam. Your certificate is being generated.");
      const certRes = await fetch("/api/certificate/generate", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          userId: resultData.userId,
          examId: resultData.examId,
          userName: resultData.userName,
          examName: resultData.examName,
          passed: resultData.passed,
        }),
        cache: "no-store",
      });

      if (certRes.ok) {
        const blob = await certRes.blob();
        const blobUrl = window.URL.createObjectURL(blob);
        window.open(blobUrl, "_blank");
      } else {
        const certJson = await certRes.json();
        toast.error("There was an error generating your certificate. Please try again later.");
        console.error(certJson.message);
      }
    } else {
      toast.error("Unfortunately, you did not pass the exam. Please try again.");
    }
  } catch (error) {
    toast.error("An unexpected error occurred during submission. Please contact support.");
    console.error("Submission error:", error);
  }
}
